body {
	font-family: Arial, Helvetica, sans-serif;
}

.dp {
	height: 42px;
	width: 42px;
	border-radius: 100px;
}

.edit-profile-card {
	height: 500px;
}

.chat-card {
	height: 500px;
}

.scroll {
	max-height: 400px;
	overflow-y: scroll;
}

.scroll::-webkit-scrollbar {
	display: none;
}

.chat-form {
	position: absolute;
	bottom: 0;
	right: 0;
	left: 0;
	padding: 15px 40px 0 20px;
	background-color: white;
}

.bubble {
	background-color: "#0275d8";
	border-radius: "50px";
}

.chat-box-border {
	border: none;
}